import { createTool } from '@mastra/core/tools';
import { z } from 'zod';
import { initializeJob, updateJobStatus, storeJobResult } from './job-status-tool';

export const webSearchTool = createTool({
  id: 'web-search-queue',
  description: 'WebÊ§úÁ¥¢„Ç∏„Éß„Éñ„Çí„Ç≠„É•„Éº„Å´ÁôªÈå≤„Åó„ÄÅjobId„ÇíÂç≥Â∫ß„Å´Ëøî„Åó„Åæ„Åô„ÄÇÂÆüÈöõ„ÅÆÊ§úÁ¥¢Âá¶ÁêÜ„ÅØ„ÉØ„Éº„ÇØ„Éï„É≠„Éº„ÅßÈùûÂêåÊúüÂÆüË°å„Åï„Çå„Åæ„Åô„ÄÇ',
  inputSchema: z.object({
    query: z.string().describe('Ê§úÁ¥¢„ÇØ„Ç®„É™'),
    maxResults: z.number().describe('ÊúÄÂ§ßÊ§úÁ¥¢ÁµêÊûúÊï∞'),
    language: z.string().describe('Ê§úÁ¥¢Ë®ÄË™û'),
  }),
  outputSchema: z.object({
    jobId: z.string().describe('„Ç∏„Éß„ÉñID'),
    status: z.literal('queued').describe('„Ç∏„Éß„Éñ„Çπ„ÉÜ„Éº„Çø„Çπ'),
    message: z.string().describe('„Çπ„ÉÜ„Éº„Çø„Çπ„É°„ÉÉ„Çª„Éº„Ç∏'),
    estimatedTime: z.string().describe('Êé®ÂÆöÂÆå‰∫ÜÊôÇÈñì'),
  }),
  execute: async ({ context, runtimeContext }) => {
    const { query, maxResults = 5, language = 'ja' } = context;
    
    // „Ç∏„Éß„ÉñID„ÇíÁîüÊàêÔºà„Çø„Ç§„É†„Çπ„Çø„É≥„Éó + „É©„É≥„ÉÄ„É†ÊñáÂ≠óÂàóÔºâ
    const jobId = `web-search-${Date.now()}-${Math.random().toString(36).substring(2, 8)}`;
    
    // „Ç∏„Éß„ÉñÁä∂ÊÖã„ÇíÂàùÊúüÂåñ
    initializeJob(jobId);
    
    // È´òÈÄü„É¨„Çπ„Éù„É≥„Çπ„ÅÆ„Åü„ÇÅ„Å´Âç≥Â∫ß„Å´jobId„ÇíËøî„Åô
    const response = {
      jobId,
      status: 'queued' as const,
      message: `WebÊ§úÁ¥¢„Ç∏„Éß„Éñ„Äå${query}„Äç„Çí„Ç≠„É•„Éº„Å´ÁôªÈå≤„Åó„Åæ„Åó„Åü`,
      estimatedTime: '30-60ÁßíÁ®ãÂ∫¶',
    };
    
    // „Éê„ÉÉ„ÇØ„Ç∞„É©„Ç¶„É≥„Éâ„Åß„ÉØ„Éº„ÇØ„Éï„É≠„Éº„ÇíÈùûÂêåÊúüÂÆüË°å
    // ÈÅÖÂª∂Ë™≠„ÅøËæº„Åø„ÅßMastra„Ç§„É≥„Çπ„Çø„É≥„Çπ„ÇíÂèñÂæó
    setTimeout(() => {
      import('../index').then(({ mastra: mastraInstance }) => {
        if (mastraInstance) {
          executeWorkflowInBackground(mastraInstance, jobId, { query, maxResults, language }, runtimeContext)
            .catch(error => {
              console.error(`„ÉØ„Éº„ÇØ„Éï„É≠„ÉºÂÆüË°å„Ç®„É©„Éº (jobId: ${jobId}):`, error);
            });
        } else {
          console.error(`Mastra„Ç§„É≥„Çπ„Çø„É≥„Çπ„ÅåÂà©Áî®„Åß„Åç„Åæ„Åõ„Çì (jobId: ${jobId})`);
          updateJobStatus(jobId, 'failed', { error: 'Mastra„Ç§„É≥„Çπ„Çø„É≥„Çπ„ÅåÂà©Áî®„Åß„Åç„Åæ„Åõ„Çì' });
        }
      }).catch(error => {
        console.error(`Mastra„Ç§„É≥„Çπ„Çø„É≥„ÇπË™≠„ÅøËæº„Åø„Ç®„É©„Éº (jobId: ${jobId}):`, error);
        updateJobStatus(jobId, 'failed', { error: 'Mastra„Ç§„É≥„Çπ„Çø„É≥„Çπ„ÅÆË™≠„ÅøËæº„Åø„Å´Â§±Êïó„Åó„Åæ„Åó„Åü' });
      });
    }, 0);
    
    return response;
  },
});

// „Éê„ÉÉ„ÇØ„Ç∞„É©„Ç¶„É≥„Éâ„Åß„ÉØ„Éº„ÇØ„Éï„É≠„Éº„ÇíÂÆüË°å„Åô„ÇãÈñ¢Êï∞
async function executeWorkflowInBackground(
  mastra: unknown, 
  jobId: string, 
  inputData: { query: string; maxResults?: number; language?: string },
  runtimeContext?: unknown
) {
  try {
    console.log(`üöÄ „ÉØ„Éº„ÇØ„Éï„É≠„ÉºÈñãÂßã (jobId: ${jobId})`);
    updateJobStatus(jobId, 'running');
    
    const mastraInstance = mastra as { getWorkflow: (id: string) => unknown };
    const workflow = mastraInstance.getWorkflow('webSearchWorkflow');
    if (!workflow) {
      throw new Error('webSearchWorkflow„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì');
    }

    const workflowInstance = workflow as { 
      createRunAsync: (options: { runId: string }) => Promise<{
        watch: (callback: (event: { type: string; payload?: { id?: string } }) => void) => void;
        start: (options: { inputData: unknown; runtimeContext?: unknown }) => Promise<{
          status: 'success' | 'failed' | 'suspended';
          result?: unknown;
          error?: { message?: string };
          suspended?: unknown;
        }>;
      }>
    };
    const run = await workflowInstance.createRunAsync({ runId: jobId });
    
    // ÈÄ≤ÊçóÁõ£Ë¶ñ„ÇíË®≠ÂÆö
    run.watch((event: { type: string; payload?: { id?: string } }) => {
      console.log(`üìä „ÉØ„Éº„ÇØ„Éï„É≠„ÉºÈÄ≤Êçó (${jobId}):`, event.type, event.payload?.id || '');
    });
    
    // „ÉØ„Éº„ÇØ„Éï„É≠„Éº„ÇíÂÆüË°åÔºàruntimeContext„ÇíÊ∏°„ÅôÔºâ
    const result = await run.start({ 
      inputData,
      runtimeContext 
    });
    
    if (result.status === 'success') {
      console.log(`‚úÖ „ÉØ„Éº„ÇØ„Éï„É≠„ÉºÂÆå‰∫Ü (jobId: ${jobId})`);
      const searchResult = result.result as { metadata?: { completedAt?: string } } | undefined;
      console.log(`üìù „É¨„Éù„Éº„ÉàÁîüÊàêÂÆå‰∫Ü: ${searchResult?.metadata?.completedAt}`);
      
      // „Ç∏„Éß„ÉñÁä∂ÊÖã„ÇíÊõ¥Êñ∞
      updateJobStatus(jobId, 'completed', { result: result.result });
      
      // „ÉØ„Éº„ÇØ„Éï„É≠„ÉºÁµêÊûú„ÇíÂ∞ÇÁî®„Çπ„Éà„É¨„Éº„Ç∏„Å´Ê†ºÁ¥ç
      storeJobResult(jobId, result.result, 'webSearchWorkflow');
      
    } else if (result.status === 'failed') {
      console.error(`‚ùå „ÉØ„Éº„ÇØ„Éï„É≠„ÉºÂ§±Êïó (jobId: ${jobId}):`, result.error);
      updateJobStatus(jobId, 'failed', { error: result.error?.message || 'Unknown error' });
      
    } else if (result.status === 'suspended') {
      console.log(`‚è∏Ô∏è „ÉØ„Éº„ÇØ„Éï„É≠„Éº‰∏≠Êñ≠ (jobId: ${jobId}):`, result.suspended);
      // ‰∏≠Êñ≠Áä∂ÊÖã„ÅØÁâπÂà•„Å™Âá¶ÁêÜ„ÅåÂøÖË¶Å„Å™Â†¥Âêà„Åå„ÅÇ„Çã„Åü„ÇÅ„ÄÅ„Åì„Åì„Åß„ÅØÁä∂ÊÖã„ÇíÊõ¥Êñ∞„Åó„Å™„ÅÑ
      
    }
    
  } catch (error) {
    console.error(`üí• „ÉØ„Éº„ÇØ„Éï„É≠„ÉºÂÆüË°å‰∏≠„ÅÆËá¥ÂëΩÁöÑ„Ç®„É©„Éº (jobId: ${jobId}):`, error);
    updateJobStatus(jobId, 'failed', { 
      error: error instanceof Error ? error.message : 'Unknown error' 
    });
  }
} 